  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

@font-face {
  font-family: "New Tegomin";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2"), url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("woff");
}
body {
  min-width: 100vw;
  min-height: 100vh;
  margin: 0;
  background-color: rgba(3, 155, 231, 0.2196078431);
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: cover;
  background-position: center center;
  font-family: "New Tegomin", serif;
  font-weight: 400;
  font-style: normal;
}

header {
  display: flex;
  justify-content: space-between;
  font-size: 19px;
  text-shadow: 2px 1px 5px #291616;
  color: white;
  background-color: rgba(132, 124, 73, 0.7294117647);
}

.tab {
  display: inline-block;
  padding: 10px;
  border-bottom: 3px solid transparent;
}
.tab:hover {
  cursor: pointer;
  color: rgba(253, 82, 82, 0.568627451);
  border-bottom: 3px solid rgba(253, 82, 82, 0.568627451);
}

.tab-active {
  color: #ffe599;
  border-bottom: 3px solid #ffe599;
}

.homepage-container {
  width: fit-content;
  padding: 20px;
  border-radius: 14px;
  margin: 50px auto;
  background-image: linear-gradient(181deg, rgba(195, 135, 83, 0.7882352941), rgba(29, 16, 6, 0.5882352941));
}
.homepage-container h1 {
  margin: 0;
  padding: 20px;
  width: fit-content;
  font-size: 72px;
  color: #33260e;
}
.homepage-container h4 {
  margin: 40px;
  font-size: 24px;
  text-align: center;
  color: white;
}

.section-container {
  width: 70%;
  max-width: 825px;
  margin: 50px auto;
  padding: 14px;
  border-radius: 14px;
  color: white;
  background-color: rgba(121, 84, 58, 0.9019607843);
}
.section-container h3 {
  margin: 0;
  font-size: 22px;
}

.menu-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 3vw;
  color: white;
}
.menu-container h2 {
  margin: 15px 0 5px;
  font-size: 26px;
  color: rgba(255, 193, 98, 0.2901960784);
  text-decoration: underline;
  text-shadow: white -2px -3px 0px;
}
.menu-container h3 {
  margin: 0;
  font-size: 21px;
}
.menu-container p {
  margin: 0 0 10px 0;
  font-size: 14px;
  line-height: 1.3;
